<template>
  <div id="new-csTemp">
    <div class="main">
      <div class="top">
        <div class="tt">创建班级模板</div>
      </div>
      <div class="concent">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="top">
          <el-form-item label="科目" prop="courseName">
            <el-input v-model="form.courseName"></el-input>
          </el-form-item>
          <el-form-item label="班级" prop="classID">
            <el-select
              filterable
              v-model="form.classID"
              placeholder="请选择班级"
              style="width:100%;"
              @change="selectClass"
            >
              <el-option
                v-for="(item, index) in classOptions"
                :key="index"
                :label="item.label"
                v-model="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button @click="$router.go(-1)">返回</el-button>
            <el-button :loading="btnLoading" type="primary" @click="saveData('form')">立即创建</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import edit from "@/mixin/edit";
export default {
  name: "",
  components: {},
  mixins: [edit],
  props: {},
  data() {
    return {
      form: {
        courseName: "",
        classID: "",
        className: ""
      },
      rules: {
        courseName: {
          required: true,
          min: 1,
          max: 60,
          message: "长度在 1 到 60 个字符",
          trigger: "blur"
        },
        classID: {
          required: true,
          message: "请选择班级",
          trigger: "blur"
        }
      },
      classOptions: []
    };
  },
  computed: {},
  watch: {},
  created() {},
  methods: {
    initDict() {
      this.classOptions = [];
      this.$http
        .get(this.$api.ClsList)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.classOptions.push({
                label: item["ClassName"],
                value: item["ClassID"]
              });
            });
          }
        })
        .catch(err => {});
    },
    selectClass(val) {
      let sclass = this.classOptions.find(r => r.value === val);
      this.form.className = sclass.label || "";
    },
    saveData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          this.$http
            .post(this.$api.CourseTemplateAdd, {
              ...this.form,
              ctID: this.$api.uuid(),
              userID: this.$http.getUser().UserID
            })
            .then(res => {
              if (res.Result) {
                this.$message({
                  message: "创建成功！",
                  type: "success"
                });
                setTimeout(() => {
                  history.go(-1);
                }, 200);
              } else if (res.message) {
                this.$message({
                  showClose: true,
                  ...res
                });
              }
              this.btnLoading = false;
            })
            .catch(err => {
              this.btnLoading = false;
            });
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    }
  }
};
</script>

<style lang='less' scoped>
#new-csTemp {
  width: 956px;
  .main {
    height: 100%;
    background: #fff;
    padding: 20px;
    text-align: left;
    .tt {
      font-size: 14px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      margin-bottom: 33px;
    }

    .concent {
      width: 320px;
      height: 450px;
      //   border: 1px solid #000;
      margin: 0 auto;
      .item {
        font-size: 14px;
        color: #333;
        .name {
          font-size: 14px;
          color: #333;
          margin-bottom: 20px;
        }
        .input {
          input,
          .el-select {
            width: 319px;
            height: 40px;
            background: rgba(236, 241, 247, 1);
            opacity: 0.8;
            border-radius: 3px;
            border: none;
            margin-bottom: 20px;
          }
          .el-input__suffix {
            height: 40px;
          }
          //   .el-input__icon{
          //       line-height: 80px;
          //   }
        }
      }
      .ft {
        width: 170px;
        margin: 0 auto;
        //   border: 1px solid #000;
        margin-top: 20px;
        display: flex;
        justify-content: space-around;
        button {
          width: 60px;
          height: 24px;
          background: rgba(42, 158, 252, 1);
          border-radius: 3px;
          font-size: 12px;
          border: none;
          color: #fff;
          margin-right: 20px;
        }
      }
    }
  }
}
</style>


